
.toastWrap{
  color: white;
  z-index:999;
  padding:5px 10px;
  border-radius: 5px;
  text-align: center;
  min-width: 100px;
  position: fixed;
  left: 50%;
  background: rgba(0,0,0,0.6);
  -webkit-transform:translate(-50%,100%)  ;
          transform:translate(-50%,100%)  ;
  /*transition: all 2s ;*/
  -webkit-animation: downTop 1s forwards,topDown 1s 2s forwards ;
          animation: downTop 1s forwards,topDown 1s 2s forwards ;
  opacity: 0;
  bottom: 100px;
}
@-webkit-keyframes downTop{
from{
}
to{
    bottom: 50%;
    -webkit-transform:translate(-50%,0)  ;
            transform:translate(-50%,0)  ;
    opacity: 1;
}
}
@keyframes downTop{
from{
}
to{
    bottom: 50%;
    -webkit-transform:translate(-50%,0)  ;
            transform:translate(-50%,0)  ;
    opacity: 1;
}
}
@-webkit-keyframes topDown{
from{
}
to{
    -webkit-transform:translate(-50%,100%)  ;
            transform:translate(-50%,100%)  ;
    opacity: 0;
}
}
@keyframes topDown{
from{
}
to{
    -webkit-transform:translate(-50%,100%)  ;
            transform:translate(-50%,100%)  ;
    opacity: 0;
}
}





