
.login-btn {
  color: white !important;
}
.bg-img {
  background-size: cover;
  background-position: center;
}
.bg-img, .bg-img img {
  width: 100%;
}
.slide_text {
  z-index: 11;
  background: rgba(0, 0, 0, 0.5);
  padding: 4px;
  color: white;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
}
.swiper-pagination-bullet {
  opacity: .6 !important;
  background: #c94a4a;
}



