
.pdf-control {
  /* background-image: url(../../assets/pdf/toolbar_background.png);*/
  height: 48px;
  overflow: hidden;
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 1;
  box-shadow: 0 -2px 2px rgba(50, 50, 50, 0.75);
  display: table;
}
.pdf-control > * {
  display: table-cell;
  text-align: center;
  vertical-align: middle;
  padding: 0 10px;
}
.pageNumber {
  background-color: rgba(0, 0, 0, 0.3);
  border: none;
  outline: none;
  text-align: center;
  color: #fff;
  vertical-align: middle;
}





