
.custome-wrap{
  position: fixed;
  width: 100%;
  height:100%;
  background: rgba(0,0,0,0.0);
  z-index:999;
}
.custome-spinner{
  background: rgba(0,0,0,0.5);
  padding: 50px;
  border-radius: 10%;
  position: absolute;
  top:50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
}
.custome-spinner>*{
  position: absolute;
  top:50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
}
.vue-simple-spinner{
  white-space: nowrap;
}



